.order-status{display:grid;grid-template-columns:1fr 30px 1fr 30px 1fr;grid-gap:30px;margin-bottom:20px;padding-top:20px;border-bottom:1px solid var(--light-gray-color);padding-bottom:10px}@media (max-width:767px){.order-status{grid-template-columns:-webkit-min-content 15px -webkit-min-content 15px -webkit-min-content;grid-template-columns:min-content 15px min-content 15px min-content;grid-gap:9px;padding-top:0}}.order-status__item{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start;-webkit-box-align:center;-ms-flex-align:center;align-items:center;cursor:default}.order-status__item--fill{cursor:pointer}.order-status__item-number{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;color:var(--white-color);width:50px;height:50px;font-size:30px;font-weight:700;border-radius:50%;margin-bottom:10px}@media (max-width:767px){.order-status__item-number{display:none}}.order-status__item--active .order-status__item-number{background:var(--red-color)}.order-status__item--unactive .order-status__item-number{background:var(--light-gray-color)}.order-status__item--fill .order-status__item-number{background:var(--light-red-color)}.order-status__item-name{font-size:13px;color:var(--black-color)}.order-status__item--active .order-status__item-name{color:var(--red-color)}.order-status__item--fill .order-status__item-name{color:var(--light-red-color)}.order-status__item--unactive .order-status__item-name{color:var(--light-gray-color)}.order-status__separator{display:block;width:100%;height:50px;fill:var(--light-gray-color)}@media (max-width:767px){.order-status__separator{height:20px}}