.rating{display:grid;grid-template-columns:-webkit-min-content -webkit-min-content;grid-template-columns:min-content min-content;grid-gap:12px;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.rating__stars{display:grid;grid-template-columns:repeat(5,15px);grid-gap:10px;-webkit-transition:none!important;-o-transition:none!important;transition:none!important}.rating__star{width:100%;height:15px;fill:var(--light-gray-color)}.rating__star,.rating__star *{-webkit-transition:none!important;-o-transition:none!important;transition:none!important}.rating--editable .rating__star{cursor:pointer}.rating__star--active{fill:var(--red-color)}.rating__text{white-space:nowrap;font-size:15px;color:var(--black-color);font-weight:600}